import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import Layout from '../components/Layout';
import botService from '../services/botService';
import './CreateBot.css';

const CreateBot = () => {
  const navigate = useNavigate();
  const [formData, setFormData] = useState({
    name: '',
    token: '',
    telegram_group_id: ''
  });
  const [loading, setLoading] = useState(false);
  const [validating, setValidating] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value
    });
    setError('');
  };

  const handleValidate = async () => {
    if (!formData.token) {
      setError('Por favor, preencha o token do bot');
      return;
    }

    setValidating(true);
    setError('');
    setSuccess('');

    try {
      const response = await botService.validateBot(formData.token);
      if (response.valid) {
        setSuccess('Token vlido!');
        setTimeout(() => setSuccess(''), 3000);
      }
    } catch (err) {
      setError(err.response?.data?.error || 'Erro ao validar token');
    } finally {
      setValidating(false);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');
    setSuccess('');
    setLoading(true);

    try {
      await botService.createBot(formData);
      setSuccess('Bot criado com sucesso!');
      setTimeout(() => {
        navigate('/');
      }, 2000);
    } catch (err) {
      setError(err.response?.data?.error || 'Erro ao criar bot');
    } finally {
      setLoading(false);
    }
  };

  return (
    <Layout>
      <div className="create-bot-page">
        <div className="create-bot-content">
          <div className="create-bot-header">
            <h1 className="create-bot-title">Crie um novo bot agora mesmo</h1>
            <p className="create-bot-subtitle">
              Aumente as vendas dos seus produtos ou servios pelo Telegram agora mesmo!
            </p>
          </div>

          <div className="create-bot-grid">
            <div className="create-bot-form-section">
              <h2 className="section-title">Criao do bot</h2>
              
              <form onSubmit={handleSubmit}>
                <div className="form-group">
                  <label htmlFor="name">Nome do bot</label>
                  <input
                    type="text"
                    id="name"
                    name="name"
                    value={formData.name}
                    onChange={handleChange}
                    required
                    placeholder="Digite o nome do bot"
                  />
                </div>

                <div className="form-group">
                  <label htmlFor="token">Token do bot</label>
                  <input
                    type="text"
                    id="token"
                    name="token"
                    value={formData.token}
                    onChange={handleChange}
                    required
                    placeholder="Digite o token do bot"
                  />
                </div>

                <div className="form-group">
                  <label htmlFor="telegram_group_id">Id do grupo no telegram</label>
                  <input
                    type="text"
                    id="telegram_group_id"
                    name="telegram_group_id"
                    value={formData.telegram_group_id}
                    onChange={handleChange}
                    placeholder="Digite o ID do grupo (opcional)"
                  />
                </div>

                {error && <div className="alert alert-error">{error}</div>}
                {success && <div className="alert alert-success">{success}</div>}

                <div className="form-actions">
                  <button
                    type="button"
                    onClick={handleValidate}
                    className="btn btn-validate"
                    disabled={validating || !formData.token}
                  >
                    {validating ? 'Validando...' : 'Validar >'}
                  </button>
                  <button
                    type="submit"
                    className="btn btn-create"
                    disabled={loading}
                  >
                    {loading ? 'Criando...' : 'Criar >'}
                  </button>
                </div>
              </form>
            </div>

            <div className="create-bot-tutorial-section">
              <h2 className="section-title">Tutorial e orientaes</h2>
              
              <div className="tutorial-content">
                <div className="tutorial-step">
                  <p>
                    1. Acesse o link abaixo para criar um bot no Telegram utilizando o BotFather e copie o token gerado.
                  </p>
                  <a
                    href="https://t.me/botfather"
                    target="_blank"
                    rel="noopener noreferrer"
                    className="tutorial-link"
                  >
                    https://t.me/botfather
                  </a>
                </div>

                <div className="tutorial-step">
                  <p>
                    Caso seja sua primeira vez criando um bot, recomendamos que assista ao nosso vdeo tutorial. 
                    Gravamos com o intuito de te orientar a preencher cada informao com total conscincia, visando 
                    a criao de um bot funcional.
                  </p>
                  <a
                    href="https://youtu.be/EoS22at6eWO"
                    target="_blank"
                    rel="noopener noreferrer"
                    className="tutorial-link"
                  >
                    https://youtu.be/EoS22at6eWO
                  </a>
                </div>

                <div className="tutorial-step">
                  <p>
                    Caso o vdeo no seja o suficiente para sanar suas dvidas, fique  vontade para acionar nosso 
                    suporte, iremos lhe ajudar com o maior prazer.
                  </p>
                </div>

                <button className="btn btn-support">
                  <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                    <path d="M21 15a2 2 0 0 1-2 2H7l-4 4V5a2 2 0 0 1 2-2h14a2 2 0 0 1 2 2z"></path>
                  </svg>
                  Acionar suporte
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default CreateBot;

