import React, { useContext } from 'react';
import { Navigate, useLocation } from 'react-router-dom';
import { AuthContext } from '../contexts/AuthContext';

/**
 * Mapeamento de rotas para menus
 */
const routeToMenuMap = {
  '/': 'dashboard',
  '/billing': 'billing',
  '/bot/create': 'bot',
  '/bot/update': 'bot',
  '/bot/welcome': 'bot',
  '/bot/payment-plans': 'bot',
  '/bot/redirect': 'bot',
  '/bot/administrators': 'bot',
  '/bot/groups': 'bot',
  '/bot/telegram-groups': 'bot',
  '/bot/': 'bot', // Para rotas que comeam com /bot/
  '/results/contacts': 'results',
  '/marketing': 'marketing',
  '/marketing/alerts': 'marketing',
  '/marketing/downsell': 'marketing',
  '/settings/payment-cycles': 'settings',
  '/settings/payment-gateways': 'settings',
  '/settings/security': 'settings',
  '/users': 'settings',
  '/user-groups': 'settings',
  '/logs': 'settings',
};

/**
 * Obtm o nome do menu baseado no path
 */
const getMenuFromPath = (pathname) => {
  // Remove query strings do path
  const pathWithoutQuery = pathname.split('?')[0];
  
  // Remove IDs numricos do path para normalizar rotas dinmicas
  const normalizedPath = pathWithoutQuery.replace(/\/\d+/g, '');
  
  // Verifica primeiro por correspondncia exata
  if (routeToMenuMap[normalizedPath]) {
    return routeToMenuMap[normalizedPath];
  }
  
  // Verifica se comea com algum prefixo conhecido (ordem especfica primeiro)
  if (normalizedPath.startsWith('/bot/update') || normalizedPath === '/bot/update') {
    return 'bot';
  }
  if (normalizedPath.startsWith('/bot/')) {
    return 'bot';
  }
  if (normalizedPath.startsWith('/results/')) {
    return 'results';
  }
  if (normalizedPath.startsWith('/marketing/')) {
    return 'marketing';
  }
  if (normalizedPath.startsWith('/settings/')) {
    return 'settings';
  }
  
  // Verifica correspondncia exata novamente (caso tenha removido algo importante)
  if (routeToMenuMap[pathWithoutQuery]) {
    return routeToMenuMap[pathWithoutQuery];
  }
  
  return null;
};

/**
 * Componente de rota protegida que verifica permisses de menu
 */
const ProtectedRoute = ({ children }) => {
  const { isAuthenticated, loading, user } = useContext(AuthContext);
  const location = useLocation();

  if (loading) {
    return <div>Carregando...</div>;
  }

  if (!isAuthenticated) {
    return <Navigate to="/login" replace />;
  }

  // Super admin tem acesso a tudo
  if (user?.user_type === 'super_admin') {
    return children;
  }

  // Se no tem grupo, no tem acesso (exceto se for super admin, j tratado acima)
  if (!user?.user_group_id) {
    return (
      <div style={{ padding: '20px', textAlign: 'center' }}>
        <h1>Acesso Negado</h1>
        <p>Voc no tem permisso para acessar esta pgina.</p>
      </div>
    );
  }

  // Obtm o menu correspondente  rota atual
  const menuName = getMenuFromPath(location.pathname);

  // Se no encontrou o menu, permite acesso (pode ser uma rota que no precisa de permisso)
  if (!menuName) {
    return children;
  }

  // Verifica se o usurio tem acesso ao menu
  const accessibleMenus = user?.accessible_menus || [];
  
  // Se tem acesso a todos os menus (*) ou ao menu especfico
  if (accessibleMenus.includes('*') || accessibleMenus.includes(menuName)) {
    return children;
  }

  // No tem permisso
  return (
    <div style={{ padding: '20px', textAlign: 'center' }}>
      <h1>Acesso Negado</h1>
      <p>Voc no tem permisso para acessar esta pgina.</p>
    </div>
  );
};

export default ProtectedRoute;

