import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import Layout from '../components/Layout';
import paymentGatewayConfigService from '../services/paymentGatewayConfigService';
import './PaymentGatewayConfigs.css';

const PaymentGatewayConfigs = () => {
  const navigate = useNavigate();
  const [botId, setBotId] = useState(null);
  const [configs, setConfigs] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const [showModal, setShowModal] = useState(false);
  const [editingConfig, setEditingConfig] = useState(null);
  const [selectedGateway, setSelectedGateway] = useState('mercadopago');
  const [selectedEnvironment, setSelectedEnvironment] = useState('test');
  const [formData, setFormData] = useState({
    gateway: 'mercadopago',
    environment: 'test',
    access_token: '',
    secret_key: '',
    webhook_secret: '',
    webhook_url: '',
    public_key: '',
    is_active: false
  });

  useEffect(() => {
    const storedBotId = localStorage.getItem('selectedBotId');
    if (!storedBotId) {
      setError('Bot no selecionado. Por favor, selecione um bot primeiro.');
      setLoading(false);
      return;
    }
    setBotId(storedBotId);
    loadConfigs(storedBotId);
  }, []);

  const loadConfigs = async (id) => {
    try {
      setLoading(true);
      const data = await paymentGatewayConfigService.getConfigs(id);
      setConfigs(data);
      setError('');
    } catch (err) {
      setError(err.response?.data?.error || 'Erro ao carregar configuraes');
    } finally {
      setLoading(false);
    }
  };

  const getConfigForGateway = (gateway, environment) => {
    return configs.find(
      c => c.gateway === gateway && c.environment === environment
    );
  };

  const handleOpenModal = (gateway, environment) => {
    setSelectedGateway(gateway);
    setSelectedEnvironment(environment);
    
    const existingConfig = getConfigForGateway(gateway, environment);
    
    if (existingConfig) {
      setEditingConfig(existingConfig);
      setFormData({
        gateway: existingConfig.gateway,
        environment: existingConfig.environment,
        access_token: existingConfig.access_token || '',
        secret_key: existingConfig.secret_key || '',
        webhook_secret: existingConfig.webhook_secret || '',
        webhook_url: existingConfig.webhook_url || '',
        public_key: existingConfig.public_key || '',
        is_active: existingConfig.is_active || false
      });
    } else {
      setEditingConfig(null);
      setFormData({
        gateway: gateway,
        environment: environment,
        access_token: '',
        secret_key: '',
        webhook_secret: '',
        webhook_url: '',
        public_key: '',
        is_active: false
      });
    }
    
    setShowModal(true);
  };

  const handleCloseModal = () => {
    setShowModal(false);
    setEditingConfig(null);
    setFormData({
      gateway: 'mercadopago',
      environment: 'test',
      access_token: '',
      secret_key: '',
      webhook_secret: '',
      webhook_url: '',
      public_key: '',
      is_active: false
    });
  };

  const handleChange = (e) => {
    const { name, value, type, checked } = e.target;
    setFormData({
      ...formData,
      [name]: type === 'checkbox' ? checked : value
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');
    setSuccess('');

    try {
      const configToSave = {
        ...formData,
        bot_id: botId
      };

      if (editingConfig) {
        await paymentGatewayConfigService.updateConfig(editingConfig.id, configToSave);
        setSuccess('Configurao atualizada com sucesso!');
      } else {
        await paymentGatewayConfigService.createOrUpdateConfig(configToSave);
        setSuccess('Configurao salva com sucesso!');
      }

      handleCloseModal();
      loadConfigs(botId);
      setTimeout(() => setSuccess(''), 3000);
    } catch (err) {
      setError(err.response?.data?.error || 'Erro ao salvar configurao');
    }
  };

  const handleDelete = async (id) => {
    if (!window.confirm('Tem certeza que deseja excluir esta configurao?')) {
      return;
    }

    try {
      await paymentGatewayConfigService.deleteConfig(id);
      setSuccess('Configurao excluda com sucesso!');
      loadConfigs(botId);
      setTimeout(() => setSuccess(''), 3000);
    } catch (err) {
      setError(err.response?.data?.error || 'Erro ao excluir configurao');
    }
  };

  const maskToken = (token) => {
    if (!token) return '';
    if (token.length <= 8) return token;
    return token.substring(0, 4) + '' + token.substring(token.length - 4);
  };

  if (loading && !botId) {
    return (
      <Layout>
        <div className="payment-gateway-configs-page">
          <div className="loading-container">Carregando...</div>
        </div>
      </Layout>
    );
  }

  if (!botId) {
    return (
      <Layout>
        <div className="payment-gateway-configs-page">
          <div className="error-container">
            <p>{error}</p>
            <button onClick={() => navigate('/')} className="btn btn-primary">
              Voltar ao Dashboard
            </button>
          </div>
        </div>
      </Layout>
    );
  }

  return (
    <Layout>
      <div className="payment-gateway-configs-page">
        <div className="payment-gateway-configs-content">
          <div className="page-header">
            <h1>Configuraes de Gateway de Pagamento</h1>
            <p>Configure as credenciais do Mercado Pago e Stripe para testes e produo</p>
          </div>

          {error && <div className="alert alert-error">{error}</div>}
          {success && <div className="alert alert-success">{success}</div>}

          {/* Mercado Pago Section */}
          <div className="gateway-section">
            <div className="gateway-header">
              <h2>Mercado Pago (PIX)</h2>
              <div className="gateway-badges">
                <button
                  className={`badge ${getConfigForGateway('mercadopago', 'test')?.is_active ? 'active' : ''}`}
                  onClick={() => handleOpenModal('mercadopago', 'test')}
                >
                  {getConfigForGateway('mercadopago', 'test') ? ' Teste' : '+ Teste'}
                </button>
                <button
                  className={`badge ${getConfigForGateway('mercadopago', 'production')?.is_active ? 'active' : ''}`}
                  onClick={() => handleOpenModal('mercadopago', 'production')}
                >
                  {getConfigForGateway('mercadopago', 'production') ? ' Produo' : '+ Produo'}
                </button>
              </div>
            </div>

            <div className="gateway-configs-grid">
              {/* Test Environment */}
              <div className="config-card">
                <div className="config-card-header">
                  <h3>Ambiente de Teste</h3>
                  {getConfigForGateway('mercadopago', 'test')?.is_active && (
                    <span className="badge-active">Ativo</span>
                  )}
                </div>
                {getConfigForGateway('mercadopago', 'test') ? (
                  <div className="config-card-content">
                    <p><strong>Access Token:</strong> {maskToken(getConfigForGateway('mercadopago', 'test').access_token)}</p>
                    <p><strong>Webhook URL:</strong> {getConfigForGateway('mercadopago', 'test').webhook_url || 'No configurado'}</p>
                    <div className="config-card-actions">
                      <button
                        className="btn btn-edit"
                        onClick={() => handleOpenModal('mercadopago', 'test')}
                      >
                        Editar
                      </button>
                      <button
                        className="btn btn-delete"
                        onClick={() => handleDelete(getConfigForGateway('mercadopago', 'test').id)}
                      >
                        Excluir
                      </button>
                    </div>
                  </div>
                ) : (
                  <div className="config-card-content">
                    <p className="empty-config">Nenhuma configurao</p>
                    <button
                      className="btn btn-primary"
                      onClick={() => handleOpenModal('mercadopago', 'test')}
                    >
                      Configurar
                    </button>
                  </div>
                )}
              </div>

              {/* Production Environment */}
              <div className="config-card">
                <div className="config-card-header">
                  <h3>Ambiente de Produo</h3>
                  {getConfigForGateway('mercadopago', 'production')?.is_active && (
                    <span className="badge-active">Ativo</span>
                  )}
                </div>
                {getConfigForGateway('mercadopago', 'production') ? (
                  <div className="config-card-content">
                    <p><strong>Access Token:</strong> {maskToken(getConfigForGateway('mercadopago', 'production').access_token)}</p>
                    <p><strong>Webhook URL:</strong> {getConfigForGateway('mercadopago', 'production').webhook_url || 'No configurado'}</p>
                    <div className="config-card-actions">
                      <button
                        className="btn btn-edit"
                        onClick={() => handleOpenModal('mercadopago', 'production')}
                      >
                        Editar
                      </button>
                      <button
                        className="btn btn-delete"
                        onClick={() => handleDelete(getConfigForGateway('mercadopago', 'production').id)}
                      >
                        Excluir
                      </button>
                    </div>
                  </div>
                ) : (
                  <div className="config-card-content">
                    <p className="empty-config">Nenhuma configurao</p>
                    <button
                      className="btn btn-primary"
                      onClick={() => handleOpenModal('mercadopago', 'production')}
                    >
                      Configurar
                    </button>
                  </div>
                )}
              </div>
            </div>
          </div>

          {/* Stripe Section */}
          <div className="gateway-section">
            <div className="gateway-header">
              <h2>Stripe (Carto de Crdito)</h2>
              <div className="gateway-badges">
                <button
                  className={`badge ${getConfigForGateway('stripe', 'test')?.is_active ? 'active' : ''}`}
                  onClick={() => handleOpenModal('stripe', 'test')}
                >
                  {getConfigForGateway('stripe', 'test') ? ' Teste' : '+ Teste'}
                </button>
                <button
                  className={`badge ${getConfigForGateway('stripe', 'production')?.is_active ? 'active' : ''}`}
                  onClick={() => handleOpenModal('stripe', 'production')}
                >
                  {getConfigForGateway('stripe', 'production') ? ' Produo' : '+ Produo'}
                </button>
              </div>
            </div>

            <div className="gateway-configs-grid">
              {/* Test Environment */}
              <div className="config-card">
                <div className="config-card-header">
                  <h3>Ambiente de Teste</h3>
                  {getConfigForGateway('stripe', 'test')?.is_active && (
                    <span className="badge-active">Ativo</span>
                  )}
                </div>
                {getConfigForGateway('stripe', 'test') ? (
                  <div className="config-card-content">
                    <p><strong>Secret Key:</strong> {maskToken(getConfigForGateway('stripe', 'test').secret_key)}</p>
                    <p><strong>Public Key:</strong> {maskToken(getConfigForGateway('stripe', 'test').public_key) || 'No configurado'}</p>
                    <p><strong>Webhook Secret:</strong> {maskToken(getConfigForGateway('stripe', 'test').webhook_secret) || 'No configurado'}</p>
                    <div className="config-card-actions">
                      <button
                        className="btn btn-edit"
                        onClick={() => handleOpenModal('stripe', 'test')}
                      >
                        Editar
                      </button>
                      <button
                        className="btn btn-delete"
                        onClick={() => handleDelete(getConfigForGateway('stripe', 'test').id)}
                      >
                        Excluir
                      </button>
                    </div>
                  </div>
                ) : (
                  <div className="config-card-content">
                    <p className="empty-config">Nenhuma configurao</p>
                    <button
                      className="btn btn-primary"
                      onClick={() => handleOpenModal('stripe', 'test')}
                    >
                      Configurar
                    </button>
                  </div>
                )}
              </div>

              {/* Production Environment */}
              <div className="config-card">
                <div className="config-card-header">
                  <h3>Ambiente de Produo</h3>
                  {getConfigForGateway('stripe', 'production')?.is_active && (
                    <span className="badge-active">Ativo</span>
                  )}
                </div>
                {getConfigForGateway('stripe', 'production') ? (
                  <div className="config-card-content">
                    <p><strong>Secret Key:</strong> {maskToken(getConfigForGateway('stripe', 'production').secret_key)}</p>
                    <p><strong>Public Key:</strong> {maskToken(getConfigForGateway('stripe', 'production').public_key) || 'No configurado'}</p>
                    <p><strong>Webhook Secret:</strong> {maskToken(getConfigForGateway('stripe', 'production').webhook_secret) || 'No configurado'}</p>
                    <div className="config-card-actions">
                      <button
                        className="btn btn-edit"
                        onClick={() => handleOpenModal('stripe', 'production')}
                      >
                        Editar
                      </button>
                      <button
                        className="btn btn-delete"
                        onClick={() => handleDelete(getConfigForGateway('stripe', 'production').id)}
                      >
                        Excluir
                      </button>
                    </div>
                  </div>
                ) : (
                  <div className="config-card-content">
                    <p className="empty-config">Nenhuma configurao</p>
                    <button
                      className="btn btn-primary"
                      onClick={() => handleOpenModal('stripe', 'production')}
                    >
                      Configurar
                    </button>
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>

        {/* Modal */}
        {showModal && (
          <div className="modal-overlay" onClick={handleCloseModal}>
            <div className="modal-content" onClick={(e) => e.stopPropagation()}>
              <div className="modal-header">
                <h2>
                  {editingConfig ? 'Editar' : 'Nova'} Configurao - {selectedGateway === 'mercadopago' ? 'Mercado Pago' : 'Stripe'} ({selectedEnvironment === 'test' ? 'Teste' : 'Produo'})
                </h2>
                <button className="modal-close" onClick={handleCloseModal}></button>
              </div>
              <form onSubmit={handleSubmit} className="modal-form">
                {selectedGateway === 'mercadopago' ? (
                  <>
                    <div className="form-group">
                      <label>Access Token *</label>
                      <input
                        type="password"
                        name="access_token"
                        value={formData.access_token}
                        onChange={handleChange}
                        required
                        placeholder="APP_USR-..."
                      />
                      <small>Token de acesso do Mercado Pago</small>
                    </div>
                    <div className="form-group">
                      <label>Webhook URL</label>
                      <input
                        type="url"
                        name="webhook_url"
                        value={formData.webhook_url}
                        onChange={handleChange}
                        placeholder="https://seu-dominio.com/api/payments/webhook/mercadopago"
                      />
                      <small>URL para receber notificaes do Mercado Pago</small>
                    </div>
                  </>
                ) : (
                  <>
                    <div className="form-group">
                      <label>Secret Key *</label>
                      <input
                        type="password"
                        name="secret_key"
                        value={formData.secret_key}
                        onChange={handleChange}
                        required
                        placeholder="sk_test_..."
                      />
                      <small>Chave secreta do Stripe</small>
                    </div>
                    <div className="form-group">
                      <label>Public Key</label>
                      <input
                        type="text"
                        name="public_key"
                        value={formData.public_key}
                        onChange={handleChange}
                        placeholder="pk_test_..."
                      />
                      <small>Chave pblica do Stripe (para uso no frontend)</small>
                    </div>
                    <div className="form-group">
                      <label>Webhook Secret</label>
                      <input
                        type="password"
                        name="webhook_secret"
                        value={formData.webhook_secret}
                        onChange={handleChange}
                        placeholder="whsec_..."
                      />
                      <small>Secret do webhook do Stripe</small>
                    </div>
                    <div className="form-group">
                      <label>Webhook URL</label>
                      <input
                        type="url"
                        name="webhook_url"
                        value={formData.webhook_url}
                        onChange={handleChange}
                        placeholder="https://seu-dominio.com/api/payments/webhook/stripe"
                      />
                      <small>URL para receber notificaes do Stripe</small>
                    </div>
                  </>
                )}
                <div className="form-group">
                  <label>
                    <input
                      type="checkbox"
                      name="is_active"
                      checked={formData.is_active}
                      onChange={handleChange}
                    />
                    Ativar esta configurao
                  </label>
                  <small>Quando ativado, esta configurao ser usada para processar pagamentos</small>
                </div>
                <div className="modal-footer">
                  <button
                    type="button"
                    onClick={handleCloseModal}
                    className="btn btn-cancel"
                  >
                    Cancelar
                  </button>
                  <button type="submit" className="btn btn-primary">
                    {editingConfig ? 'Atualizar' : 'Salvar'}
                  </button>
                </div>
              </form>
            </div>
          </div>
        )}
      </div>
    </Layout>
  );
};

export default PaymentGatewayConfigs;

